/* Card styling */

.card {
  display: inline-block;
  margin-bottom: $grid-margin-after;
  overflow: hidden;
  background-color: $white;
  box-shadow: $drop-shadow;
  width: 100%;
}

.card__inner {
  padding: $padding-y $padding-x $padding-y-bottom;
}

.card__image {
  border: none;
  height: 200px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  object-fit: cover;
}

.card__title {
  @include heading(26);
  margin-bottom: $header-margin-after-less;
}

.card__content {
  font-weight: 400;
}

.card button {
  margin-top: $space-y-half;
}

// Card sizes.
// Adjust a bit based on breakpoints.
@include breakpoint(medium) {
  .card--small {
    max-width: 320px;
  }

  .card--medium {
    max-width: 440px;
  }
}
@include breakpoint(large) {
  .card--large {
    max-width: 640px;
  }
}
